@media screen and (max-width: 767px) {
  .social {
    padding-bottom: 10px;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.social__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-left: 5px;
  padding: 0;

  @media screen and (max-width: 1023px) {
    max-width: 350px;
  }

  @media screen and (max-width: 767px) {
    justify-content: center;
    max-width: 700px;
  }
}

.social__item {
  width: 50px;
  height: 50px;
  margin-right: 5px;
  text-align: center;
  margin-top: 9px;
  list-style-type: none;

  @media screen and (max-width: 1023px) {
    margin-right: 4px;
  }
}

.social__link {
  cursor: pointer;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}
